// Calculs des totaux par catgorie
export const calculateTotalsByCategory = () => {
  const totals = {
    Courant: 0,
    Mensuel: 0,
    Annuel: 0,
    pargne: 0
  }

  defaultReferenceBudgets.forEach(budget => {
    if (budget.category && totals.hasOwnProperty(budget.category)) {
      totals[budget.category as keyof typeof totals] += budget.value
    }
  })

  return totals
}

// Total gnral des budgets (hors pargne)
export const getTotalBudgetAmount = () => {
  const totals = calculateTotalsByCategory()
  return totals.Courant + totals.Mensuel + (totals.Annuel / 12) // Annuel divis par 12 pour avoir le montant mensuel
}

// Total de l'pargne
export const getTotalSavingsAmount = () => {
  const totals = calculateTotalsByCategory()
  return totals.pargne
} 